import { Component, OnInit } from '@angular/core';
import { SubscriptionService } from '../subscription.service';
import {ApiService} from '../api.service'
import { TransitiveCompileNgModuleMetadata } from '@angular/compiler';

@Component({
  selector: 'app-userlist',
  templateUrl: './userlist.component.html',
  styleUrls: ['./userlist.component.css']
})
export class UserlistComponent implements OnInit {
name="kalyani";
responsedict={};
  columnDefs: any;
  rowData: any;
  constructor(private sub:SubscriptionService,private api :ApiService) { }

  ngOnInit(): void {
    this.columnDefs = [
      { field: 'firstname', sortable: TransitiveCompileNgModuleMetadata,checkboxSelection: true },
      { field: 'lastname' },
      { field: 'username' ,filter:true},
      { field: 'role' }
  ];

  this.rowData = [
      { firstname: 'anil', lastname: 's', username: 'anils',role:'student'  },
      { firstname: 'akshay', lastname: 'm', username: 'akshaym' ,role:'student' },
      { firstname: 'arun', lastname: 'p', username: 'arunp',role:'student' }
  ];
   // this.callAPIusers();

  }
  senddata(){

    console.log(this.name)
    var dict={};
    dict["name"]=this.name;
    dict["company"]='TCS';

    
   
    this.api.adduser(dict).subscribe(data=>{
      alert(data)
      this.callAPIusers();
     

    });
this.sub.getObservable(this.name);
  }

  callAPIusers(){
    this.api.callusers().subscribe(data=>{
      this.responsedict=data;
      
     

    });

  }


}
